header {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

img {
    height: 150px;
    width: 150px;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: pink;
    border-radius: 20px;
    padding: 30px;
    margin: 10px;
}

.board {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}